// pages > index.js  
import React, { useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import { Button, Card, Popover, Avatar, List, Comment } from 'antd';
import { HeartTwoTone, EllipsisOutlined, HeartOutlined, MessageOutlined, RetweetOutlined } from '@ant-design/icons';
import { useDispatch, useSelector } from 'react-redux';
import Link from 'next/link';
import moment from 'moment';

import PostImages from './PostImages';
import CommentForm from './CommentForm';
import PostCardContent from './PostCardContent';
import FollowButton from './FollowButton';

import { REMOVE_POST_REQUEST, LIKE_POST_REQUEST, UNLIKE_POST_REQUEST, RETWEET_REQUEST } from '../reducers/post';

//import moment from 'moment';
moment.locale('ko'); //    (:)

const PostCard = ({ post }) => { // post: post   'mainPosts ' 
    const dispatch = useDispatch();
    // const [liked, setLiked] = useState(false);//   
    const [commentFormOpened, setCommentFormOpened] = useState(false); //    

    const id = useSelector((state) => state.user.me?.id); // user :   
    const { removePostLoading } = useSelector((state) => state.post); // post :   

    const liked = post.Likers.find((v) => v.id === id);

    // useEffect(() => { if (retweetError) { alert(retweetError); } }, [retweetError]); //      index.js  .
    //------------------------------------------------
    const onLike = useCallback(() => { // 
        if (!id) { return alert(' .'); }
        return dispatch({ type: LIKE_POST_REQUEST, data: post.id });
    }, [id]);

    const onUnlike = useCallback(() => { //  
        if (!id) { return alert(' .'); }
        return dispatch({ type: UNLIKE_POST_REQUEST, data: post.id });
    }, [id]);

    const onToggleComment = useCallback(() => { setCommentFormOpened((prev) => !prev); }, []); //   

    const onRemovePost = useCallback(() => { //   
        if (!id) { return alert(' .'); }
        return dispatch({ type: REMOVE_POST_REQUEST, data: post.id });
    }, [id]);

    const onRetweet = useCallback(() => { //  
        if (!id) { return alert(' .'); }
        return dispatch({ type: RETWEET_REQUEST, data: post.id });
    }, [id]);

    return (
        <div style={{ marginBottom: 20 }}>
            {/*-------------------------------------------------- , (, , , & or ) */}
            <Card
                cover={post.Images[0] && <PostImages images={post.Images} />}
                actions={[ //  jsx      .
                    <RetweetOutlined key="retweet" onClick={onRetweet} />,

                    liked
                        ? <HeartTwoTone twoToneColor="#eb2f96" key="heart" onClick={onUnlike} />
                        : <HeartOutlined key="heart" onClick={onLike} />,

                    <MessageOutlined key="comment" onClick={onToggleComment} />,

                    <Popover
                        key="more"
                        content={(
                            <Button.Group>
                                {id && post.User.id === id
                                    ? (<><Button></Button> <Button type="danger" loading={removePostLoading} onClick={onRemovePost}></Button></>)
                                    : <Button></Button>}
                            </Button.Group>
                        )}>
                        <EllipsisOutlined />
                    </Popover>,
                ]}
                title={post.RetweetId ? `${post.User.nickname} .` : null}
                extra={id && <FollowButton post={post/*   id   */} />}
            >
                {/*--------------------------------------------------   */}
                {post.RetweetId && post.Retweet
                    ? (
                        <Card cover={post.Retweet.Images[0] && <PostImages images={post.Retweet.Images} />}>
                            <div style={{ float: 'right' }}>{moment(post.createdAt).format('YYYY.MM.DD')}</div>
                            <Card.Meta
                                avatar={(
                                    <Link href={`/user/${post.Retweet.User.id}`}>
                                        <a><Avatar>{post.Retweet.User.nickname[0]}</Avatar></a>
                                    </Link>
                                )}
                                title={post.Retweet.User.nickname}
                                description={<PostCardContent postData={post.Retweet.content} />}
                            />
                        </Card>
                    )
                    : (
                        <>
                        <div style={{ float: 'right' }}>{moment(post.createdAt).fromNow()}</div>
                        <Card.Meta
                            avatar={(
                                <Link href={`/user/${post.User.id}`}>
                                    <a><Avatar>{post.User.nickname[0]}</Avatar></a>
                                </Link>
                            )}
                            title={post.User.nickname}
                            description={<PostCardContent postData={post.content} />}
                        />
                        </>
                    )}
            </Card>
            {/*-------------------------------------------------- (  ) */}
            {commentFormOpened && (
                <div>

                    <CommentForm post={post/*      */} />

                    <List
                        style={{ marginTop: 40 }}
                        header={`${post.Comments.length} `}
                        itemLayout="horizontal"
                        dataSource={post.Comments}
                        renderItem={(item) => (
                            <li>
                                <Comment
                                    author={item.User.nickname}
                                    avatar={(
                                        <Link href={`/user/${item.User.id}`}>
                                            <a><Avatar>{item.User.nickname[0]}</Avatar></a>
                                        </Link>
                                    )}
                                    content={item.content}
                                />
                            </li>
                        )}
                    />
                </div>
            )}
            {/* <CommentForm /> */}
            {/* <Comments /> */}
        </div>
    );
};

PostCard.propTypes = {
    post: PropTypes.shape({
        id: PropTypes.number,
        User: PropTypes.object,
        content: PropTypes.string,
        createdAt: PropTypes.string,
        Comments: PropTypes.arrayOf(PropTypes.object),
        Images: PropTypes.arrayOf(PropTypes.object),
        Likers: PropTypes.arrayOf(PropTypes.object),
        RetweetId: PropTypes.number,
        Retweet: PropTypes.objectOf(PropTypes.any),
    }).isRequired,
};

export default PostCard;